:root {
	/* --primary-color: #005fff; */
	--primary-color-alpha: #005fff1a;
}

html,
body {
	margin: 0;
	padding: 0;
	height: 100%;
}

.stream_chat_container {
	width: 100vw;
	background-image: url("../../assets/section_5.svg");
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 120px 40px 80px 40px;
	transform: scaleX(-1);
}

.stream_chat {
	width: 100%;
	position: relative;
	max-width: 1500px;
	font-size: 16px;
}

.app__wrapper {
	width: 100%;
	display: flex;
	flex: 1;
	height: 650px !important;
	/* border-radius: 16px; */
	box-shadow: rgba(0, 0, 0, 0.33) 0px 1px 4px 0px;
	transform: scaleX(-1);
}

.chat_unavailable_box {
	display: none;
}

@media screen and (max-width: 600px) {
	.app__wrapper {
		display: none !important;
	}

	.chat_unavailable_box.chat_unavailable_box {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: rgba(255, 255, 255, 0.1);
		backdrop-filter: blur(5px);
		text-align: center;
		border-radius: 12px;
		width: 120%;
		padding: 50px 20px 50px 20px;
		transform: scaleX(-1);
	}
}

.auth__form-container {
	min-height: 100vh;
	display: flex;
	flex-direction: row;
}

.auth__form-container_fields {
	flex: 2;
	display: flex;
	flex-direction: column;
	justify-content: center;

	padding: 2rem;
	background: #005fff;
}

.auth__form-container_image {
	flex: 3;
	display: flex;
	box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.05);
}

.auth__form-container_image img {
	width: 100%;
	height: 100%;
}

.auth__form-container_fields-content {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;

	padding: 2rem;
	box-shadow: 0px 1px 5px rgb(0 0 0 / 10%);
	border-radius: 5px;
	transition: 0.8s ease;
	background: #fff;
}

.auth__form-container_fields-content p {
	font-size: 1.5rem;
	font-family: Arial, Helvetica, sans-serif;
	color: #05245a;
	font-weight: 900;
}

.auth__form-container_fields-content_input {
	display: flex;
	flex-direction: column;
	position: relative;

	margin: 1rem 0rem;
}

.auth__form-container_fields-content_input label {
	margin-bottom: 0.45rem;
	color: rgb(61, 79, 88);
	font-size: 12px;
	font-family: Arial, Helvetica, sans-serif;
	letter-spacing: 0.7px;
	line-height: 1.3;
}

.auth__form-container_fields-content_input input {
	padding: 0.55rem 0.4rem;
	border: 1px solid rgb(184, 196, 194);
	border-radius: 4px;
	font-size: 14px;
	outline: none;
	transition: all 150ms ease-in-out 0s;
	width: 85%;
	background: #fff;
}

.auth__form-container_fields-content_input input::placeholder {
	color: #b1b1b1;
	width: 100%;
	font-weight: unset;
	font-family: Arial, Helvetica, sans-serif;
}

.auth__form-container_fields-content_input input:hover {
	border-color: #dcdddd;
}

.auth__form-container_fields-content_input input:focus,
.auth__form-container_fields-content_input input:active {
	box-shadow: 0px 0px 0px 1.5px #005fff;
	border-color: #005fff;
}

.auth__form-container_fields-content_input-password {
	position: absolute;
	right: 13%;
	top: 50%;
	cursor: pointer;
}

.auth__form-container_fields-content_button {
	margin-top: 2rem;
	display: flex;
	justify-content: flex-start;
}

.auth__form-container_fields-content_button button {
	border-radius: 4px;
	background: #005fff;
	border: 1px solid #005fff;
	color: #fff;
	font-family: Arial, Helvetica, sans-serif;
	font-weight: 500;
	padding: 0.7rem 1.2rem;
	outline: none;
	cursor: pointer;
	transition: 0.3s ease;
}

.auth__form-container_fields-content_button button:hover {
	background: #0066ff;
}

.auth__form-container_fields-account {
	display: flex;
	justify-content: flex-start;
	align-items: center;

	margin-top: 0.2rem;
}

.auth__form-container_fields-account p {
	font-size: 14px;
	color: #000;
	font-weight: 500;
}

.auth__form-container_fields-account span {
	color: #05245a;
	cursor: pointer;
	font-weight: 700;
}

@media screen and (max-width: 800px) {
	.auth__form-container {
		flex-direction: column-reverse;
	}

	.auth__form-container_fields {
		justify-content: flex-start;
	}

	.auth__form-container_image {
		height: 100px;
		flex: none;
		box-shadow: none;
	}

	.auth__form-container_image img {
		object-fit: cover;
	}
}

@media screen and (max-width: 375px) {
	.auth__form-container_fields {
		padding: 2rem 0.5rem;
	}

	.auth__form-container_fields-content_input input {
		width: 95%;
	}

	.auth__form-container_fields-content_input-password {
		right: 3%;
	}
}

.str-chat-channel-list {
	height: fit-content;
	margin-bottom: 8px;
	background: #0b0c0e !important;
	padding: 0px 10px 50px 0px;
}

.str-chat-channel {
	height: 100%;
	border-top-right-radius: 12px !important;
	border-bottom-right-radius: 12px !important;
}

.str-chat__load-more-button {
	display: none;
}

.str-chat__input-footer {
	display: none;
}

.str-chat__date-separator {
	margin: 16px 24px;
}

.str-chat__message-notification {
	background: var(--primary-color);
	cursor: pointer;
}

@media screen and (max-width: 960px) {
	.str-chat-channel-list.team {
		position: unset;
		left: unset;
		top: unset;
		z-index: unset;
		min-height: unset;
		overflow-y: unset;
		box-shadow: unset;
		transition: unset;
	}
}

.channel-empty__container {
	display: flex;
	height: 100%;
	flex-direction: column;
	justify-content: flex-end;
	margin-left: 20px;
	margin-right: 20px;
	padding-bottom: 20px;
}

.channel-empty__avatars {
	display: flex;
}

.channel-empty__avatars div:first-child {
	z-index: 3;
}

.channel-empty__avatars div:nth-child(2) {
	position: relative;
	right: 32px;
	z-index: 2;
}

.channel-empty__avatars div:nth-child(3) {
	position: relative;
	right: 64px;
	z-index: 1;
}

.channel-empty__avatars .str-chat__avatar {
	margin-right: 0;
}

.channel-empty__first {
	font-family: Helvetica Neue, sans-serif;
	font-weight: bold;
	font-size: 18px;
	line-height: 120%;
	color: #858688;
	margin-bottom: 10px;
}

.channel-empty__first .channel-empty__user-name {
	color: var(--primary-color);
}

.channel-empty__second {
	font-family: Helvetica Neue, sans-serif;
	font-size: 14px;
	line-height: 120%;
	margin: 0;
	color: #858688;
}

.channel__container {
	height: 100%;
	width: 100%;
	background: rgb(17, 20, 24);
	border-top-right-radius: 12px !important;
	border-bottom-right-radius: 12px !important;
}

.str-chat__thread {
	z-index: 1;
}

.str-chat__thread-list .str-chat__message-team {
	border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.str-chat__thread-list .str-chat__message-actions-list button:first-child {
	display: none;
}

.str-chat__list .str-chat__reverse-infinite-scroll {
	padding-top: 0px;
}

.str-chat__thread-list .channel-empty__container {
	display: none;
}

.str-chat__date-separator {
	padding: 20px 40px;
}

.custom-thread-header {
	height: 62px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.1);
}

.custom-thread-header__left {
	display: flex;
	align-items: center;
	margin-left: 20px;
}

.custom-thread-header__left-title {
	font-family: Helvetica Neue, sans-serif;
	font-weight: bold;
	font-size: 18px;
	line-height: 22px;
	color: #2c2c30;
	margin-right: 10px;
}

.custom-thread-header__left-count {
	font-family: Helvetica Neue, sans-serif;
	font-size: 14px;
	color: #858688;
}

.close-thread-icon {
	cursor: pointer;
	margin-right: 10px;
}

.str-chat__list--thread .str-chat__message-simple__actions__action--options .str-chat__message-actions-box {
	left: initial;
	right: 100%;
	border-radius: var(--border-radius-md);
}

.channel-list__container {
	display: flex;
