	align-items: center;
	justify-content: center;
}

.channel-list__header {
	background: #00010c;
	text-align: center;
	height: 62px;
	border-top-left-radius: 12px;
}

.channel-list__header__text {
	font-family: var(--Poppins);
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 62px;
	color: #6cc027;
}

.channel-search__container {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 16px 10px 16px 10px;
	border-top: 1px solid #00000033;
}

.channel-search__input__wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 40px;
	background: rgba(83, 83, 83, 0.2);
	border-radius: 8px;
	margin-bottom: 8px;
	border: 1px solid transparent;
	width: 95%;
}

.channel-search__input__wrapper:focus-within {
	border: 2px solid #6cc027;
}

.channel-search__input__icon {
	width: 32px;
	display: flex;
	justify-content: center;
}

.channel-search__input__text {
	background: none;
	border: none;
	color: #6cc027;
	font-family: Helvetica Neue, sans-serif;
	font-size: 16px;
	outline: none;
	width: 90%;
}

::placeholder {
	color: rgba(255, 255, 255, 0.5);
	white-space: nowrap;
	width: 150px;
}

.channel-search__results {
	position: absolute;
	height: fit-content;
	width: 300px;
	background: #242427;
	border: 1px solid #17171a;
	box-sizing: border-box;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06);
	border-radius: 8px;
	z-index: 10;
	left: 230px;
	top: 16px;
}

.channel-search__results-header {
	width: fit-content;
	display: flex;
	align-items: center;
	font-family: Helvetica Neue, sans-serif;
	font-style: normal;
	font-weight: 500;
	font-size: 14px;
	line-height: 120%;
	color: #989a9c;
	margin-left: 12px;
	padding-top: 20px;
}

.channel-search__results-header i {
	font-weight: normal;
	margin-left: 6px;
}

.channel-search__results-header_loading {
	padding-top: 8px;
	padding-bottom: 8px;
}

.channel-search__result-container {
	width: 100%;
	height: 40px;
	display: flex;
	align-items: center;
}

.channel-search__result-container__focused {
	width: 100%;
	height: 48px;
	display: flex;
	align-items: center;
	background: #1b1b1d;
}

.channel-search__result-container:hover {
	background: #1b1b1d;
	cursor: pointer;
}

.channel-search__result-container:hover:last-child {
	border-bottom-left-radius: 6px;
	border-bottom-right-radius: 6px;
}

.channel-search__result-user {
	display: flex;
	align-items: center;
	margin-left: 12px;
}

.result-hashtag {
	height: 24px;
	width: 28px;
	background: var(--primary-color);
	border-radius: 24px;
	margin: 12px 0px 12px 12px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: Helvetica Neue, sans-serif;
	font-weight: bold;
	font-size: 14px;
	line-height: 120%;
	color: #17171a;
}

.channel-search__result-text {
	margin: 0px;
	padding: 0px;
	width: 100%;
	font-family: Helvetica Neue, sans-serif;
	font-weight: 500;
	font-size: 14px;
	line-height: 100%;
	color: #adadaf;
	margin-left: 8px;
}

.channel-search__result-container:hover .channel-search__result-text {
	color: #6cc027;
}

.create-channel__container {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.create-channel__header {
	display: flex;
