	align-items: center;
	justify-content: center;
	z-index: 1000;
}
.rfu-file-previewer__close-button {
	position: relative;
	z-index: 10000;
}
.rfu-file-previewer__failed {
	background: #ff6363;
}
.rfu-file-previewer__failed,
.rfu-file-previewer__retry {
	padding: 3px 6px;
	margin-left: 8px;
	color: #fff;
	border-radius: 4px;
	font-size: 12px;
}
.rfu-file-previewer__retry {
